@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.header{
    position: -webkit-sticky;
    position: sticky;
    background-color: #1f1f1f;
    color: #fff;
    font-size: 16px;
    top: 0;
    left: 0;
    flex: 0 0 auto;
    z-index: 10;
    padding-bottom: 10px;
}
a{
    color: #fff;
}
.burger-wrapper{
    display: none;
}
.burger-img{
    font-size: 26px;
    cursor: pointer;
}

.header-inner, .header-nav, .nav-list{
    display: flex;
    align-items: center;
}
.header-inner{
    padding-top: 10px;
    justify-content: space-between;
}
.header-nav{
    padding-right: 10px;
}
.nav-list{
    margin: 0;
    padding-left: 0;
    margin-right: 20px;
}
.nav-list li{
    margin-right: 15px;
}
@media (max-width: 769px) {
    .burger-wrapper{
        display: block;
    }
    .nav-list{
        display: none;
    }
}
.mobile-menu-inner{
    height: 100%;
    background-color: #1f1f1f;
    position: fixed;
    max-width: 300px;
    width: 60%;
    border-radius: 0 20px 20px 0;
    z-index: 100;
    top: 0;
    left: 0;
    -webkit-transform: translateX(-300px);
            transform: translateX(-300px);
    -webkit-transition: all 800ms cubic-bezier(.8, 0, .33, 1);
    transition: all 800ms cubic-bezier(.8, 0, .33, 1);
    font-size: 22px;
}
.mobile-menu-inner.mobile-menu-inner__open{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
.overlay{
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0;
    -webkit-transition: all 800ms ease-in;
    transition: all 800ms ease-in;
}
.overlay-show{
    opacity: 0.5;
}
.mobile-menu-list{
    min-height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.mobile-menu-list li{
    padding-top: 5px;
    padding-bottom: 5px;
}
.app{
    font-family: 'Roboto', sans-serif;
    display: flex;
    flex-direction: column;
    height: 100vh;
}
.container{
    max-width: 1024px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
}

a{
    text-decoration: none;
}
a:hover{
    color: #f60;
}
li{
    list-style: none;
}
.main-wrapper{
    flex: 1 0 auto;
}
.main{
    padding-top: 40px;
    padding-bottom: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
}
.image-error{
    color: red;
    font-size: 18px;
    padding: 5px;
    position: absolute;
}

@media (max-width: 769px) {
    .main{
        grid-template-columns: 1fr;
        align-items: center;
        justify-content: center;
    }
}
.form{
  max-width: 800px;
  width: 90%;
  margin-top: 40px;
  display: flex;
  justify-content: space-around;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}
.form__input{
  flex-basis: 50%;
  padding: 5px 20px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid #1f1f1f;
  outline: none;
}
.form__input:focus{
  border-color: #ff6600;
}
.form__btn{
  flex-basis: 30%;
  align-items: start;
  border: none;
  background-color: #1f1f1f;
  color: #ffffff;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 20px;
  cursor: pointer;
  margin-left: 10px;
}
.form__btn:hover{
  background-color: #ff6600;
}

.card-wrapper {
  width: 290px;
  height: 290px;
  border-style: solid;
  border-color: #fff;
  border-width: 10px;
  position: relative;
  margin: 0 auto;
  box-shadow: -1px 2px 11px 0px rgba(0,0,0,0.75);
}

.card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}
.image-container-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #1f1f1f;
  
}

