.deleter-card{
  position: absolute;
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  top: -9px;
  right: -9px;
  padding: 3px;
  background-color: rgba(0,0,0,0.25);
  border-radius: 5px;
}
.deleter-card:hover{
  background-color: rgba(0,0,0,0.85);
}
.api-wrapper{
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.error-api{
  color: #ff6600;
  font-size: 2.5rem;
}
.repo-card{
  background-color: #fff;
  font-size: 16px;
  color: #000;
  padding: 20px 30px;
  margin-top: 20px;
  margin-bottom: 20px;
box-shadow: -1px 2px 11px 0px rgba(0,0,0,0.75);
}
.repo-card a {
  display: inline-block;
  padding: 15px 10px;
  background-color: #1f1f1f;
  margin-top: 10px;
}
.repo-desc{
  padding-top: 10px;
  padding-bottom: 10px;
}
.error-404-wrapper{
    width: 100%;
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.error-404 {
    color: #ff6600;
    font-size: 2.5rem;
}
.footer{
    box-sizing: border-box;
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: #1f1f1f;
    color: #fff;
    flex: 0 0 auto;
    font-size: 18px;
}
.footer li {
    margin-top: 5px;
    margin-bottom: 5px;
}
.footer a{
    margin-left: 5px;
}
